var querystring = require('querystring'),
    request = require('../services/request');

module.exports = function (config) {
  var credentials = config.credentials;
  config = Object.assign(config, {
    apiFeature: '/venues'
  });
  return {
    categories: function categories(params) {
      var urlString = config.apiUrl + config.apiFeature + "/categories?" + querystring.stringify(credentials);
      return request(urlString);
    },
    explore: function explore(params) {
      var urlString = config.apiUrl + config.apiFeature + "/explore?" + querystring.stringify(params) + '&' + querystring.stringify(credentials);
      return request(urlString);
    },
    recommendations: function recommendations(params) {
      var urlString = config.apiUrl + "/search/recommendations?" + querystring.stringify(params) + '&' + querystring.stringify(credentials);
      return request(urlString);
    },
    trending: function trending(params) {
      var urlString = config.apiUrl + config.apiFeature + "/trending?" + querystring.stringify(params) + '&' + querystring.stringify(credentials);
      return request(urlString);
    },
    getVenues: function getVenues(params) {
      var urlString = config.apiUrl + config.apiFeature + "/search?" + querystring.stringify(params) + '&' + querystring.stringify(credentials);
      return request(urlString);
    },
    getVenue: function getVenue(params) {
      var urlString = config.apiUrl + config.apiFeature + "/" + params.venue_id + '?' + querystring.stringify(credentials);
      return request(urlString);
    },
    getVenuePhotos: function getVenuePhotos(params) {
      var urlString = config.apiUrl + config.apiFeature + "/" + params.venue_id + '/photos?' + querystring.stringify(credentials);
      return request(urlString);
    },
    getVenueTips: function getVenueTips(params) {
      var urlString = config.apiUrl + config.apiFeature + "/" + params.venue_id + '/tips?' + querystring.stringify(credentials);
      return request(urlString);
    },
    suggestCompletion: function suggestCompletion(params) {
      var urlString = config.apiUrl + config.apiFeature + "/suggestcompletion?" + querystring.stringify(params) + '&' + querystring.stringify(credentials);
      return request(urlString);
    }
  };
};